.section {

position: fixed;
width: 100%;
padding: 15px 0;
background-color: blue;
margin-top: -8px;
margin-left: -8px;

}

.container {

padding: 60px 15px 15px 15px;
width: 1200px;
margin: 0 auto;

}

.search-form {
display: flex;
justify-content: center;
}

.gallery {

display: flex;
flex-wrap: wrap;
gap: 30px;
flex-basis: calc((100% - 90px) / 4);

}

.thumb {
height: 180px;
width: 270px;
}

.thumb > img {

display: block;
height: 100%;
width: 100%;
object-fit: cover;

}

.photo-card {

box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14),
  0px 2px 1px rgba(0, 0, 0, 0.2);
border-radius: 0px 0px 4px 4px;
  
}

.info {

display: flex;
justify-content: space-around;

}

.info-item {

display: flex;
flex-direction: column;
justify-content: center;
align-items: center;
font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
font-size: 13px;

}

.load-more.is-hidden {
display: none;
}

.load-more {

display: block;
width: 150px;
height: 40px;
margin: 30px auto;
color: white;
background-color: blue;
border-color: blue;
border-radius: 8px;

}

.load-more:hover {

cursor: pointer;
background-color: rgb(65, 65, 252);
border-color: rgb(65, 65, 252);

}

.submit {
cursor: pointer;
}
